.title__block {

  p {
    padding: 24px 0 80px;
  }

}

.item__wrap {
  display: flex;
  align-items: center;
  gap: 40px;

  .post-image {
    border-radius: 20px;
    overflow: hidden;
  }

  .post-info {

    flex-basis: 510px;

    h3 {
      letter-spacing: -0.96px;
    }

    p {
      padding: 32px 0 16px;
    }

    .post-details {
      display: flex;
    }

    .details-author {
      display: flex;
    }

  }
}